import _ from "lodash"
var cu = window.cu;


var ub = {

    ctx: {
        tb: {}
    },

    run: function (model, data, getter ) {
        model = _.cloneDeep( model );
        let obj = model.mold.tpl;

        this.applyOps( model.mold.ops, obj , data,  getter );


        if ( obj.props ) 
            obj.props.tplid = model.uid;
        

        return obj;
    },

    applyOps : function ( ops, obj, data, getter ) {
        let get = getter || _.get;

        let newOps = [];
        for (var i = 0; i < ops.length; i++) {

            let op = ops[i];

            if (op.type == "value") {
                let value = get(data, op.action, op, newOps);
                _.set(obj, op.path, value)
            }
            else if (op.type == "computedValue") {
                let value = op.value;
                _.set(obj, op.path, value)
            }
            else if (op.type == "concat") {
                let value = get(obj, op.path, op, newOps).join("");
                _.set(obj, op.path, value)
            }
        }

        if ( newOps.length ) 
            this.applyOps( newOps, obj, data, getter )
    },


}


