DataManager.setCombined("ub.flow", {
  start: function () {
    this.wallet = {
      step: { createdAt: new Date().getTime() },
    }
  },

  parse: function (input) {
    if ( input.indexOf( ".s") == 0 ) {


      this.wallet.step.showSearch = true;
      this.wallet.step.noPlace = true;
    }
    else {
      this.wallet.step.showSearch = false;
      this.wallet.step.noPlace = false;
    }

    var result = input.match(/^(\.[^\s]+\s+)*/)
    var text = input.substr(result[0].length)
    var dirs = result[0].trim().split(" ");

    if (!this.wallet.step.editStartedAt) this.wallet.step.firstCharAt = new Date().getTime();

    this.wallet.step.text = text;
  },

  place: function () {
    let ant = {
      text: this.wallet.text,
      id: myUB.antsDbState.getId(),
      times: {
        createdAt: this.wallet.step.createdAt,
        firstCharAt: this.wallet.step.firstCharAt,
        addedAt: new Date().getTime()
      }
    };

    let parent = this.getParent();
  }

})

var flow = {

  start: function () {
    this.wallet = {
      step: {},
      actions: this.main,
      parser: this.parser
    }
  },

  restart: function () {
    let prevStep = this.wallet.step;
    this.wallet = {
      step: {
        parent: prevStep.parent
      },
      startedAt: new Date().getTime(),
      actions: this.main,
      parser: this.parser
    }
  },

  parse: function (text) {
    this.wallet.parser.parse(this.wallet, text)
  },

  change: function (values) {
    this.wallet.actions.change(this.wallet, values)
  },

  place: function () {
    this.wallet.actions.place(this.wallet)
  },

  parser: {
    parse: function (wallet, text) {
      wallet.actions.change(wallet, { text: text, x: text.substr(1) })
    }
  },

  main: {
    place: function (wallet) {
      if (wallet.step.text == "destroy") {
        myUB.db.destroy();
        return;
      }
      else if (wallet.step.text.indexOf(".m") == 0) {
        let id = this.createMap(wallet.step.text.substr(2).trim());
        hapi.selectMap(id);
        return;
      }
      else if (wallet.step.text.indexOf(".e") == 0) {
        let id = this.changeItem({ text : wallet.step.text.substr(2).trim() }, wallet);
        return;
      }
      else if (wallet.step.text.indexOf(".i") == 0) {
        let id = this.addInfo({ text : wallet.step.text.substr(2).trim() }, wallet);
        return;
      }
      else if ( wallet.step.text.indexOf(".t") == 0 ) {
        let text = wallet.step.text.substr( 2 ).trim();
        let root = DataManager.getWallet("repo.root");
        let parent = root.id;

        wallet.step.text = text;
        wallet.step.parent = parent;
        wallet.step.selectItem = true;
      }

      this.add(wallet)

      myUB.afterRenderActions.add("alertAnimation");
    },
    change: function (wallet, values) {
      _.merge(wallet.step, values)
    },

    createMap: function (name) {
      let id = myUB.antsDbState.getId();
      let ant = {
        id: id,
        text: name,
        startedAt: myUB.antsDbState.getTime(),
        type: "map",

        rels: {
          sub: {},
          allSubs: {}
        },
        mapId : id,
        parentId : id
      }

      myUB.mapview.mergeItem(ant.id, ant);
      DataManager.set("repo.items." + ant.id, ant);

      return ant.id;
    },

    changeItem : function ( change, wallet ) {
      let id = wallet.step.parent;
      if ( !id ) return;

      let ant = DataManager.getWallet("repo.items." + id);

      _.merge(ant, change);

      if ( !ant.history )  ant.history = []
      ant.history.push( { changeAt : Date.now() });

      myUB.mapview.mergeItem(id, ant);
      DataManager.set("repo.items." + id, ant);

    },

    addInfo : function ( change, wallet ) {
      let id = wallet.step.parent;
      if ( !id ) return;

      let ant = DataManager.getWallet("repo.items." + id);

      _.merge(ant, change);

      if ( !ant.history )  ant.history = []
      ant.history.push( { changeAt : Date.now() });

      myUB.mapview.mergeItem(id, ant);
      DataManager.set("repo.items." + id, ant);

    },

    add: function (wallet) {

      let parent = wallet.step.parent;
      let root = DataManager.getWallet("repo.root");

      if (!parent) {
        parent = root.id;
      }

      let state = DataManager.getWallet("repo.state");
      state.nextId++;

      let id = myUB.antsDbState.getId();

      let ant = {
        text: wallet.step.text,
        id: id,
        startedAt: wallet.startedAt,
        placedAt: new Date().getTime(),
        mapId : root.id,
        parentId : parent 
      }


      myUB.mapview.mergeItem(id, ant);
      myUB.mapview.mergeRoot(_.set({}, "rels.sub." + parent + "." + id, { id: id, parent: parent }))
      myUB.mapview.mergeRoot(_.set({}, "rels.allSubs." + id, { id: id, parent: parent }))

      DataManager.set("repo.items." + id, ant);
      DataManager.set("repo.root.rels.sub." + parent + "." + id, { id: id, parent: parent })
      DataManager.set("repo.root.rels.allSubs." + id, { id: id, parent: parent })

      if ( wallet.step.selectItem ) {
        hapi.select( id );
      }
    }
  }
}
flow.start();


let myUB = window.myUB = {};

DataManager.setCombined("fnsub", {
  loader: {
    get: function () {

      let fns = myUB;

      if (!fns.fnsub || !fns.fnsub.isReady) {
        this.make(fns, "fnsub", "ub.fnsub", {
          old: fns.fnsub
        })

        this.setupAll(fns);
      }

      fns.fnsub.makeUb("mapview", "ub.mapView", {
        "tb.db": "db",
        "state": "antsDbState",
      })

      fns.fnsub.makeUb("antsDbState", "ub.antsDbState", {
        "tb.db": "db"
      })


      fns.fnsub.makeUb("bridgeLoader", "ub.loaders.bridge", {
        "tb.db": "db"
      })

      fns.fnsub.makeUb("searchLoader", "ub.loaders.search", {
        "tb.db": "db"
      })


      fns.fnsub.makeUb("db", "ub.db", {})

      fns.fnsub.makeUb("afterRenderActions", "ub.afterRenderActions", {})
      fns.fnsub.setupAll();

    },

    setupAll: function (fns) {
      for (var i in fns) {
        if (fns[i].goSetup)
          this.setup(fns, fns[i])
      }

      return true;
    },

    make: function (fns, name, path, extra) {
      let obj = Object.create(DataManager.getValue(path));
      obj.goSetup = true;
      obj.extra = extra;
      obj.uid = cu.uids.run();

      if (fns[name]) _.merge(fns[name], obj)
      else
        fns[name] = obj;
    },

    setup: function (fns, obj) {
      delete obj.goSetup;

      let values = {};
      for (var i in obj.extra) {
        let target = _.isString(obj.extra[i]) ? _.get(fns, obj.extra[i]) : obj.extra[i];
        if (!target) {
          target = {};
          _.set(fns, obj.extra[i], target)
        }
        _.set(values, i, target)
      }

      if (obj.setup)
        obj.setup(values);

      obj.isReady = true;
    },

    autostart: true

  }
})

myUB.fnsub = {
  subs: [],
  waiting: {},
  makeUb: function (a, b, c) {
    this.subs.push([a, b, c])
  },
  onready: function (sub) {
    let payload = {};
    let promise = new Promise((resolve) => {
      payload.resolve = resolve;
    })

    if (!this.waiting[sub.uid]) this.waiting[sub.uid] = [];

    this.waiting[sub.uid].push(payload);

    return promise;
  }
}


