DataManager.setCombined("ub.mapView", {
  setup: function (args) {
    this.wallet = {
      items: {},
      root: {
        id: "ant1",
        text: "welcome",
        rels: { sub: {} },
        type: "map",
        notdb : true,
      },
      changed: {},
      state: { nextId: 1 }
    };

    this.wallet.items.ant1 = this.wallet.root;

    _.merge(this, args)

    return this;
  },
  loadMap: function (id) {
    let startIds = [id, "info_" + id]

    let root = DataManager.getWallet("repo.root")

    let start = Promise.resolve();

    if (root.id !== id) {
      if (!root.notdb)
        start = start.then(  () => this.saveItems() ) ;


      start = start.then ( ()=> {
      this.wallet.items = {}
      this.wallet.root = {
        id: id,
        notdb: true,
        rels: { sub: {} },
      }
      this.wallet.items[id] = this.wallet.root;
      DataManager.set("repo.items", this.wallet.items)
      DataManager.set("repo.root", this.wallet.root)
    })
    }

    start.then( ()=> this.tb.db.loadItems(startIds) )
      .then(items => {
        for (var i in items) {
          let item = items[i];
          if (!item) continue;
          this.wallet.items[items[i].id] = items[i];
        }
        if (items[0]) {
          let item = items[0];
          this.wallet.root = item;
          if ( true || !item.brand ) item.brand = cu.brand.getBrand(item.id + item.startedAt)

          let customTpl =  {
            logo : cu.brand.getLogo( item.id + item.startedAt,  10, 5)
          }
          let customMolds = cu.tplObjParse.run(customTpl);
          DataManager.setCombined( "customTpl", customTpl);

        }
      })
      .then(() => {
        let root = this.wallet.root;
        let related = _.get(root, "rels.allSubs") || {};
        let ids = Object.keys(related);
        return this.tb.db.loadItems(ids)
      })
      .then(items => {
        for (var i in items) {
          let item = items[i];
          if (!item) continue;
          this.wallet.items[item.id] = item;
        }
      })
      .then(() => {
        console.log("done", this.wallet)

        DataManager.set("repo.items", this.wallet.items)
        DataManager.set("repo.root", this.wallet.root)

        //notify;
      })
  },

  getItemsToSave: function () {
    let changed = this.wallet.changed;
    this.wallet.changed = {};

    let items = [];
    for (var i in changed) {
      if (this.wallet.items[i].notdb) continue;
      items.push(this.wallet.items[i])
    }
    return items;
  },
  saveItems: function () {
    let items = this.getItemsToSave();
    return this.tb.db.saveItems(this.uid, items);
  },
  generateId: function () {
    return this.tb.state.getId();
  },
  mergeItem: function (id, value) {
    if (!id) id = this.generateId();
    if (!this.wallet.items[id]) this.wallet.items[id] = { id: id, createdAt: new Date().getTime() };
    _.merge(this.wallet.items[id], value);
    this.triggerChange(id);
    return id;
  },
  mergeRoot: function (value) {
    _.merge(this.wallet.root, value);
    this.triggerChange(this.wallet.root.id);

  },

  triggerChange: function (id) {
    this.wallet.changed[id] = true;
    this.tb.db.request_save(this.uid, this);
  },

  setRel: function (rel, a, b, value) {
    _.set(this.wallet.root, ["rels", rel, a, b], value);
    this.triggerChange(this.wallet.root.id)
  },

})
