DataManager.setCombined("antView.map", {
  nodes: {
    loader: {
      get: function (tb, v) {
        tb.fetch({
          uistate: "repo.uistate",
          items: "repo.items",
          root: "repo.root",
          repo: "repo",
          molds: "antView.molds",
          customTpl : "customTpl"
        }, v);

        v.rows = Object.values(v.items);

        let rows = [];

        v.data = {}
        v.actions = cu.store.get(tb, hapi, { runWith: { select: ["select", "antid"] } });

        v.selected = v.uistate.selected;

        v.color = "#007abc"
        v.otherTpl = "map.other";
        v.moldsMap = ["map.topic", "map.item", "map.entry"]
        v.cardMold = "map.card";
        v.background = "#000";

        v.colors = {
          background : "#000"
        }

        v.body = this.getBody(v, v.root.id, v.root.rels.sub);
        return v.body;
      },

      setV: function (value, v, memo) {
        memo = memo || {};
        for (var i in value) {
          if (memo[i] === undefined)
            memo[i] = v[i];
          v[i] = value[i]
        }

        return memo;
      },
      cleanV: function (v, memo) {
        for (var i in memo)
          v[i] = memo[i];
      },

      getBody: function (v, id, sub) {
        let list = this.getBodyList(v, id, sub);

        let logo = v.customTpl.logo ? cu.reactAssembler.run(v.customTpl.logo, v) : "";
        let memo = this.setV({ logo: logo, item: v.repo.root , id : v.repo.root.id}, v, memo)

        let title = cu.reactAssembler.run(v.molds.mapView.title, v);
        memo = this.setV({ content: list, title: title }, v, memo)
        let node = cu.reactAssembler.run(v.molds.mapView.frame, v)
        this.cleanV(v, memo);
        return node;
      },
      getBodyList: function (v, id, sub) {
        let related = sub[id];

        if (!related) return null;
        if (v.level > 5) return null;
        let level = v.level != undefined ? v.level + 1 : 0

        let list = [];
        let memo = this.setV({
          id: null,
          item: null,
          level: 0,
          isSelected: null,
          content: null,
          title: null,
          handle : null,
          afterContent : null
        }, v, memo);


        let count = 1;
        for (var i in related) {

          _.merge(v, {
            id: i,
            item: v.items[i],
            level: level,
            isSelected: v.selected == i ? "selected" : "",
            count : count , 
            afterContent : null
          })

          let item = this.getItem(v, sub);
          list.push(item);

          count++;
        }

        this.cleanV(v, memo);
        return list;
      },

      getHandle : function ( v, level, count ) {
        let handle;
        if ( level == 0 ) {
          handle =  String.fromCharCode(64 + count) 
        }

        else if ( level == 1 )  {
          handle =  count + "" ;
        }

          if ( handle == undefined ) return "";

          let node =  cu.reactAssembler.run( v.molds.handle, { handle : handle });

          return node;
        },

      getItem: function (v, sub) {
        let item = v.item;
        let moldName = v.mold;
        if (!moldName && v.moldsMap) {
          moldName = v.moldsMap[v.level]
        }

        if (!moldName) moldName = v.otherTpl;
        let titleMold = _.get(v.molds, moldName);

        v.handle = this.getHandle( v, v.level, v.count );

        v.title = cu.reactAssembler.run(titleMold, v);
        v.content = this.getBodyList(v, v.id, sub);

        if (v.isSelected && !v.content) v.content = [];


        let node;
        if (v.content) {
          let cardMold = _.get(v.molds, v.cardMold);

          node = cu.reactAssembler.run(cardMold, v)
        }
        else node = v.title;

        return node;
      },



    }
  }
})




